  -moz-appearance: textfield;
} */



#get-in-touch {
  color: var(--Black-700, #00131B);
  font-size: 2.25rem;
  font-weight: 800;
  line-height: 3.25rem;
  letter-spacing: -0.025rem;
}

#get-in-touch-des {
  align-self: stretch;

  color: var(--Black-500, #667176);
  font-size: 1rem;
  font-style: normal;
  font-weight: 400;
  line-height: 1.75rem;
}

.customer-form {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  gap: 1.5rem;
  align-self: stretch;
}

.details-inputTextArea {
  display: flex;
  padding: 0.625rem 1rem;
  justify-content: center;
  align-items: center;
  gap: 0.5rem;
  align-self: stretch;

  border-radius: 0.25rem;
  border: 0.063rem solid var(--Black-300, #D9DCDD);
  background: var(--White, #FFF);
}

#submit-button {
  display: flex;
  padding: 0.875rem 2rem;
  justify-content: center;
  align-items: center;
  gap: 0.625rem;
  align-self: stretch;
  border-radius: 3.125rem;
  background: var(--Blue-400, #009AD6);
  border: 0;

  color: var(--White, #FFF);
  text-align: center;

  font-size: 1.125rem;
  font-style: normal;
  font-weight: 500;
  line-height: 1.75rem;

  transition: all 400ms;
  top: 0;
}

#submit-button:hover {
  cursor: pointer;
}

